@import "../../style/__colors";

#sidebar {
  margin-top: 14px;
  width: 275px;
  max-width: 275px;
  padding-left: 10px;
  .nav-items {
    ul {
      margin-top: 10px;
      li {
        width: 100%;
        position: relative;
        height: 60px;
        padding: 17px 0px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        &:not(.btn-tweet):hover {
          background-color: $dark3;
          border-radius: 20px;
        }
        span {
          position: absolute;
          left: 60px;
          font-weight: 700;
        }
      }
    }
  }
}
